import React from 'react';
import './Menu.css';
import { Link } from 'react-router-dom';

function Menu() {
    return (
      <div className="Menu">
            <Link to="/"><i class="fas fa-user fa-2x" /></Link>    
            <Link to="/Education"><i class="fas fa-graduation-cap fa-2x" /></Link>
            <Link to="/Skill"><i class="fas fa-gamepad fa-2x" /></Link>
            <Link to="/Project"><i class="fas fa-book fa-2x" /></Link>
      </div>
    );
  }
  
  export default Menu;