import React from 'react';
import './Cat.css';

function Cat() {
    return (
        <div className="Cat">
            <span className="LeftEar"></span>
            <span className="RightEar"></span>
            <span className="LeftEye"></span>
            <span className="RightEye"></span>
            <span className="Tail"></span>
            <span className="Shadow"></span>
        </div>
    );
}

export default Cat;