import React from 'react';
import './Box.css';
import { Switch, Route } from 'react-router-dom';
import Profile from './Profile'
import Education from './Education'
import Skill from './Skill'
import Project from './Project'

function Box() {
    return (
        <div className="box">
            <Switch>
                <Route path="/Education">
                    <Education />
                </Route>
                <Route path="/Skill">
                    <Skill />
                </Route>
                <Route path="/Project">
                    <Project />
                </Route>
                <Route path="/">
                    <Profile />
                </Route>
            </Switch>
        </div>
    );
  }
  
  export default Box;